.blog-content__inner
  display: flex
  flex-direction: column
  gap: 60px
  padding: 60px 0
  @include onDesktopSm
    gap: 45px
    padding: 45px 0

.content-list
  display: grid
  grid-template-columns: repeat(3, 1fr)
  gap: 60px 32px
  @include onDesktopSm
    gap: 30px 16px
  @include onTablet
    grid-template-columns: repeat(2, 1fr)
  @include onMobileSm
    grid-template-columns: repeat(1, 1fr)
  &:not(.not-first-page) li:first-child
    grid-column: 1 / 4
    @include onTablet
      grid-column: 1 / 3
    @include onMobileSm
      grid-column: 1 / 2
    .content-item
      flex-direction: row
      align-items: center
      background-color: var(--white)
      padding: 16px
      padding-right: 80px
      gap: 80px
      border-radius: 8px
      border: 1px solid #ddd
      @include onDesktopSm
        gap: 28px
        padding-right: 40px
      @include onMobile
        flex-direction: column
        padding-right: 0
        padding: 8px
        border-radius: 8px
        gap: 16px
      .content-img
        max-width: 712px
        width: 100%
        height: 468px
        @include onDesktopSm
          max-width: 450px
          height: 240px
        @include onMobile
          max-width: 100%
          height: auto
          border-radius: 8px
        @include onMobileSm
          max-width: 100%
          height: 280px
      .content-text
        gap: 0
        h2
          font-size: 40px
          font-weight: 500
          padding: 24px 0
          line-height: 1.3
          b
            font-size: 72px
            color: var(--text-dark)

          @include onDesktopSm
            padding: 10px 0
            line-height: 1.3
            b
              font-size: 40px
          @include onMobile
            padding: 0

        p
          display: block
          font-size: 24px
          font-weight: 400
          line-height: 1.5
        .btn
          margin-top: 40px
          @include onDesktopSm
            margin-top: 14px
          @include onMobile
            font-size: 14px
            padding: 8px 16px
        .link
          position: relative
          display: flex
          align-items: center
          width: fit-content
          gap: 8px
          margin-top: 8px
          font-size: 24px
          line-height: 1.5
          font-weight: 400
          font-family: 'Poppins', sans-serif
          color: var(--text-blue)
          &::before
            content: ""
            position: absolute
            width: 100%
            height: 1px
            background-color: var(--text-blue)
            left: 50%
            bottom: -3px
            transform: translateX(-50%)
          img
            width: 24px
            object-fit: contain
        .date,
        .place
          font-size: 24px
          font-weight: 400
          @include onDesktopSm
            font-size: 18px
          @include onMobile
            font-size: 14px
        a
          display: flex
          width: fit-content
        @include onDesktopSm
          h2
            font-size: 26px
          p
            font-size: 16px
          .link
            font-size: 20px
        @include onMobile
          gap: 10px
          width: 100%
          h2
            font-size: 18px
            b
              color: var(--text-pink)
          .link
            font-size: 14px
          .btn
            margin: 0 auto 24px
        @include onMobileSm
          width: 100%
          .date
            margin-bottom: -10px
  
.content-item
  display: flex
  flex-direction: column
  gap: 24px
  .content-img
    position: relative
    display: block
    width: 100%
    height: 388px
    overflow: hidden
    border-radius: 16px
    .event-first__label.label
      position: absolute
      bottom: 20px
      right: 20px
      display: flex
      flex-direction: column
      padding: 24px
      border-radius: 16px
      background: rgba(255, 255, 255, 0.08)
      backdrop-filter: blur(24px)
      @include onMobile
        padding: 8px
        right: 10px
        bottom: 10px
        border-radius: 8px
      .label__title
        display: flex
        flex-direction: column
        align-items: end
        color: #FFFFFF
        p
          text-transform: capitalize
      @include onMobileSm
        border-radius: 16px
    img
      width: 100%
      height: 100%
      object-fit: cover
      object-position: center
      transition: 0.4s
    &:not(:first-child)
      &:hover img
        transform: scale(1.1)
  .content-text
    display: flex
    flex-direction: column
    gap: 16px
    h2
      font-size: 24px
      font-weight: 400
      line-height: 1.5
    p, a
      display: none
    .date,
    .place
      font-size: 16px
      line-height: 1.6
      font-weight: 400
      color: var(--text-gray-date)
    .date
      order: -1
  @include onDesktopSm
    gap: 18px
    .content-img
      height: 200px
    .content-text
      gap: 10px
      h2
        font-size: 18px
      .date,
      .place
        font-size: 14px

.blog-wrapper
  .content-text
    .date
      order: initial
  li:first-child
    grid-column: 1 / 4
    @include onTablet
      grid-column: 1 / 3
    @include onMobileSm
      grid-column: 1 / 2
    .content-item
      flex-direction: row
      align-items: center
      background-color: var(--white)
      padding: 16px
      padding-right: 80px
      gap: 80px
      border-radius: 8px
      border: 1px solid #ddd
      @include onDesktopSm
        gap: 30px
        padding-right: 40px
      @include onMobile
        gap: 20px
        padding: 10px
      @include onMobileSm
        flex-direction: column
        padding-right: 0
        border: none
        background-color: transparent
        padding: 0
        gap: 16px
      .content-img
        max-width: 712px
        width: 100%
        height: 468px
        @include onDesktopSm
          max-width: 450px
          height: 240px
        @include onMobile
          max-width: 300px
          height: 180px
          border-radius: 8px
        @include onMobileSm
          max-width: 100%
          height: 280px
          border-radius: 16px
      .content-text
        gap: 24px
        h2
          font-size: 40px
          line-height: 1.3
        p
          display: block
          font-size: 24px
          font-weight: 400
          line-height: 1.5
        .link
          position: relative
          display: flex
          align-items: center
          width: fit-content
          gap: 8px
          margin-top: 8px
          font-size: 24px
          line-height: 1.5
          font-weight: 400
          font-family: 'Poppins', sans-serif
          color: var(--text-blue)
          &::before
            content: ""
            position: absolute
            width: 100%
            height: 1px
            background-color: var(--text-blue)
            left: 50%
            bottom: -3px
            transform: translateX(-50%)
          img
            width: 24px
            object-fit: contain
        .date,
        .place
          display: none
        @include onDesktopSm
          gap: 8px
          h2
            font-size: 28px
          p
            font-size: 16px
          .link
            font-size: 20px
        @include onMobile
          gap: 10px
          h2
            font-size: 18px
          .link
            font-size: 14px

.blog-recommend__inner
  padding: 80px 0
  h3
    font-size: 40px
    margin-bottom: 48px
    font-weight: 500
    @include onTablet
      font-size: 32px
      margin-bottom: 30px

.blog-recommend__list
  display: grid
  grid-template-columns: repeat(3, 1fr)
  gap: 32px
  width: 100%
  @include onDesktopSm
    gap: 16px
  @include onMobile
    display: flex
    flex-direction: column
    gap: 42px
  li
    flex-grow: 1
    a
      display: flex
      flex-direction: column
      gap: 24px
      @include onDesktopSm
        gap: 16px
      img
        width: 100%
        height: 350px
        object-fit: cover
        border-radius: 16px
        @include onDesktopSm
          height: 250px
        @include onTablet
          height: 210px
        @include onMobile
          height: 300px
      p
        font-size: 28px
        font-weight: 600
        line-height: 1.4
        transition: 0.2s
        @include onDesktopSm
          font-size: 22px
        @include onTablet
          font-size: 18px
        @include onMobile
          font-size: 28px
      &:hover
        p
          color: var(--text-pink)
