:root
    //font
    --main-font-family: 'Qanelas', sans-serif

    //text
    --white: #FFFFFF
    --text-yellow: #FFD400
    --text-pink: #FF007B
    --text-dark: #001140
    --text-tag: #3F9EF5
    --text-blue: #3F54F5
    --text-input-label: #929CB2
    --text-gray-date: #979797

    //bg

    --bg-main: #001140
    --bg-blue: #3F54F5
    --bg-gradient: linear-gradient(180deg, #EDF2FF 0%, #FFF 100%)
    --bg-blue-gradient: linear-gradient(180deg, #DCE5FF 0%, #FFF 100%)
    --bg-button-pink: #242936
    --bg-button-active: #FF007B
    --border-form: #E8F1FC
    --border-white: rgba(255, 255, 255, 0.3)

    // box-shadow
    --shadow-button-pink: 0px 12px 48px 0px rgba(255, 0, 123, 0.08)
    --shadow-button-pink-hover: 0px 20px 60px 0px rgba(255, 0, 123, 0.32)
    --shadow-button-pink-active: 0px 4px 10px 0px rgba(0, 17, 64, 0.20) inset
    --shadow-button-pink-hover-sm: 0px 16px 60px 0px rgba(255, 0, 123, 0.32)
    --shadow-button-pink-active-sm: 0px 2px 10px 0px rgba(0, 17, 64, 0.20) inset
    --shadow-dropdown: 0px 20px 40px 0px rgba(0, 0, 0, 0.30)
