@mixin respond-to($breakpoint)
    @if $breakpoint == phone-small
        @media only screen and (max-width: 25em)  
            @content  // 400px

    @if $breakpoint == phone
        @media only screen and (max-width: 37.5em)  
            @content  // 600px

    @if $breakpoint == tab-port-small
        @media only screen and (max-width: 48em)  
            @content  // 768px

    @if $breakpoint == tab-port
        @media only screen and (max-width: 56.25em)  
            @content  // 900px

    @if $breakpoint == tab-land
        @media only screen and (max-width: 75em)  
            @content  // 1200px

    @if $breakpoint == big-desk
        @media only screen and (max-width: 112.5em)  
            @content  // 1800px

    @if $breakpoint == ultra-big-desk
        @media only screen and (min-width: 156.25em)  
            @content  // 1800px
            
    @if $breakpoint == ultra-ultra-big-desk
        @media only screen and (min-width: 160.25em)  
            @content  // 2564px
            
    @if $breakpoint == ultra-ultra-ultra-big-desk
        @media only screen and (min-width: 225em)  
            @content  // 3600px

@mixin onDesktopLg
  @media (min-width: 1921px)
    @content

@mixin onDesktop
  @media (min-width: 1600px)
    @content

@mixin onDesktopSm
  @media (max-width: 1600px)
    @content

@mixin onTabletLg
  @media (max-width: 1280px)
    @content

@mixin onTablet
  @media (max-width: 991px)
    @content

@mixin onMobile
  @media (max-width: 768px)
    @content

@mixin onMobileSm
  @media (max-width: 568px)
    @content
