.solutions__block
  position: relative
  &::before
    content: ''
    position: absolute
    top: 8%
    right: 10%
    display: block
    width: 494px
    height: 608px
    background-image: url(../img/solutions-bg.png)
    background-repeat: no-repeat
    background-size: contain
    animation: rotating 15s linear infinite
    transform-origin: 40% center
    @media screen and (min-width: 2160px)
      right: 20%
    @media screen and (min-width: 2880px)
      right: 30%
    @media screen and (min-width: 3200px)
      right: 40%
    @include onTablet
      display: none
  
  &.bg-dark
    color: var(--white)
    @include onTablet
      .solutions__block-container .solutions__list .solution__container
        background: rgba(63, 84, 245, 0.30)
        border: 1px solid rgba(63, 84, 245, 0.15)
  .solutions__block-container
    display: flex
    flex-direction: column
    gap: 56px
    @include onDesktopSm
      gap: 24px
    .title__block
      display: flex
      flex-direction: column
      gap: 40px
      max-width: 1146px
      @include onDesktopSm
        gap: 16px
        max-width: 100%
      h2, h4
        color: #001140
        b, strong
          color: var(--text-pink)
          font-weight: 500
    .solutions__list
      display: flex
      align-items: stretch
      counter-reset: solutions
      @include onMobile
        margin-right: -16px
      .slick-list
        width: 100%
        margin: 0 -54px
        .slick-slide
          margin: 0 54px
        @include onDesktopSm
          margin: 0 -16px 0 0
          .slick-slide
            margin: 0 16px 0 0
      .slick-dots
          display: flex
          justify-content: center
          bottom: -34px
          li
            display: block
            width: 24px
            height: 8px
            border-radius: 4px
            background: rgba(63, 84, 245, 0.30)
            transition: 0.2s
            @include onMobile
              height: 4px
            button
              width: 100%
              height: 100%
              &::before
                content: ''
          .slick-active
            display: block
            width: 40px
            height: 8px
            border-radius: 4px
            background: var(--text-yellow)
            @include onMobile
              height: 4px
            button
              width: 100%
              height: 100%
              &::before
                content: ''
      .solution__container
        display: flex
        flex-direction: column
        gap: 15px
        position: relative
        height: auto
        @include onTablet
          gap: 24px
          padding: 24px 24px 30px
          margin-right: 16px
          max-width: 100%
          border-radius: 8px
          border: 1px solid rgba(63, 84, 245, 0.15)
          background: var(--white)
        .solution__img-container
          max-height: 300px
          img, object
            object-fit: contain
            object-position: left
            height: 100%
          @include onTabletLg
            max-height: 220px
          @include onMobile
            max-width: 178px
        .solution__text
          display: flex
          flex-direction: column
          gap: 24px
          flex-grow: 1
          @include onDesktopSm
            gap: 8px
          h4
            @include onDesktopSm
              font-size: 24px
              font-weight: 500
              line-height: 30px
          .solution__link
            display: flex
            align-items: center
            gap: 8px
            max-width: fit-content
