.intro__block
  background: var(--bg-main)
  position: relative
  display: flex
  align-items: center
  padding-top: 112px
  padding-bottom: 60px
  z-index: 2
  overflow: hidden
  //min-height: 100vh
  flex-direction: column
  @include onTabletLg
    padding-top: 86px
  @include onMobile
    padding-top: 68px
  .container
    justify-content: center
    display: flex
    flex-grow: 1
    flex-direction: column
  &::before
    content: ''
    display: block
    position: absolute
    top: -53%
    left: -41%
    width: 1900px
    height: 1900px
    background-image: url(../img/intro-elipse.png)
    background-repeat: no-repeat
    background-size: contain
    animation: rotating 10s linear infinite
    transform-origin: center 25%
    @include onDesktopSm
      top: -50%
      left: -110%
    @include onMobile
      width: 819px
      height: 819px
      top: -16%
      left: -40%
    @media screen and (min-width: 2880px)
      top: -55%
      left: -10%
    @media screen and (min-width: 3200px)
      top: -50%
      left: 0
  &.main
    padding-bottom: 52px
    position: relative
    &::before
      content: ''
      display: block
      position: absolute
      top: -53%
      left: -41%
      width: 1900px
      height: 1900px
      background-image: url(../img/intro-elipse.png)
      background-repeat: no-repeat
      background-size: contain
      animation: rotating 10s linear infinite
      transform-origin: center 25%
      @include onDesktopSm
        top: -50%
        left: -110%
      @include onTablet
        left: -150%
      @include onMobile
        width: 819px
        height: 819px
        top: -16%
        left: -156%
      @media screen and (min-width: 2880px)
        top: -55%
        left: -10%
      @media screen and (min-width: 3200px)
        top: -50%
        left: 0
    .slider .slide img
      filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7489%) hue-rotate(198deg) brightness(101%) contrast(103%)
    @include onDesktopLg
      max-height: 1440px
    @include onTablet
      max-height: fit-content
  @include onTablet
    height: fit-content
  &.main
    padding-bottom: 52px
    @include onTablet
      .intro__block-container
        flex-direction: column-reverse
        &::before,
        &::after
          content: none
        .intro__block-img
          display: block
    @include onMobileSm
      .intro__block-container .intro__block-img
        padding: 0 45px
        img
          height: auto
  &.with-search
    min-height: auto
    height: 100%
    max-height: 520px
    .intro__block-text
      align-items: center
      gap: 60px
      padding: 80px 0
      h1
        font-weight: 400
      @include onDesktopSm
        gap: 40px
        padding: 40px 0
        justify-content: center
      @include onTablet
        gap: 20px
        padding: 20px 0
  &.article
    background-color: var(--bg-main)
    overflow: visible
    min-height: 991px
    @include onDesktopSm
      min-height: 700px
    .intro__block-container::before
      content: none
    .intro__block-text
      justify-content: center
      max-width: 1330px
      margin: 0 auto 280px
      @include onDesktopSm
        margin: 0 0 80px
        max-width: 680px
    &::before
      top: 50%
      right: 0
      left: auto
      transform: translateY(-50%)
      width: 676px
      height: 676px
      background-image: url(../img/intro-mini-elipse-2.svg)
      @include onMobile
        width: 450px
        height: 450px
  &.bg-dark-gradient
    background: radial-gradient(77.56% 50% at 50% 50%, #011E70 0%, #001140 100%)

.intro__main-conteiner
  height: 100%
  display: flex
  flex-direction: column
  justify-content: space-between
  flex-grow: 1
  @include onTablet
    gap: 48px

.intro__main-conteiner > .container
  flex-grow: 1

.intro__block-container
  display: flex
  justify-content: space-between
  align-items: center
  gap: 12px
  height: 100%
  position: relative
  min-height: 244px
  &.half-sides
    .intro__block-img
      max-width: 100%
  @include onTablet
    flex-direction: column-reverse
    justify-content: normal
    min-height: 144px
    gap: 16px
    &::before
      content: ""
      position: absolute
      width: 516px
      height: 516px
      border-radius: 50%
      transform: rotateX(50deg) translateY(-50%)
      top: 50%
      right: -25%
      border: 75px solid rgba(255, 255, 255, 0.05)
    &::after
      content: ""
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      display: block
      max-width: 700px
      max-height: 700px
      border-radius: 500px
      width: 100%
      height: 100%
      opacity: 0.5
      background: var(--bg-blue)
      filter: blur(250px)

.intro__block-text
  display: flex
  flex-direction: column
  gap: 48px
  width: 100%
  flex-grow: 1
  position: relative
  z-index: 2
  form
    width: 100%
    max-width: 1063px
  .btn
    width: fit-content
    @include onMobile
      width: 100%
  @include onDesktopSm
    gap: 32px
  @include onTablet
    justify-content: space-between
    height: 100%

.text__inner
  display: flex
  flex-direction: column
  gap: 32px
  color: var(--white)
  h1 b,
  h2 b,
  h1 strong,
  h2 strong
    color: var(--text-yellow)
    font-weight: 500
  h1
    @include onMobile
      font-size: 40px
      line-height: 1.25
      font-weight: 500
  h3
    font-size: 40px
    line-height: 1.25
    font-weight: 500
    @include onDesktopSm
      font-size: 24px
  h4
    font-size: 24px
    font-weight: 400
    line-height: 1.5
    @include onDesktopSm
      font-size: 16px
  em
    white-space: nowrap
    font-style: normal
    @include onMobile
      white-space: normal
  &.less-width
    p
      max-width: 80%
    @include onTablet
      p 
        max-width: 100%
  @include onDesktopSm
    gap: 24px

.intro__block-img
  width: 100%
  max-width: 657px
  position: relative
  z-index: 2
  &::before
    content: ""
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    display: block
    max-width: 700px
    max-height: 700px
    border-radius: 500px
    width: 100%
    height: 100%
    opacity: 0.5
    background: var(--bg-blue)
    filter: blur(250px)
    z-index: -1
  img
    width: 100%
    height: auto
    object-fit: contain
  svg
    width: 100%
    height: 100%
    object-fit: contain