
.slider
  border-top: 1px solid rgba(0, 17, 64, 0.3)
  border-bottom: 1px solid rgba(0, 17, 64, 0.3)
  width: 100%
  display: flex
  >.slide
    min-width: 320px
  &.has-mb
    margin-bottom: 100px
    @include onDesktopSm
      margin-bottom: 52px
    @include onMobile
      margin-bottom: 48px
  &.brand-slider
    padding: 30px 0
    @include onDesktopSm
      padding: 20px 0
  &.on-top
    border-top: none
    &.brand-slider
      padding: 28px 0
  &.side-slider
    width: 1250px
    border: none
    padding-bottom: 58px
    @include onDesktopLg
      width: 1600px
    @include onDesktopSm
      width: 900px
      padding-bottom: 40px
    @include onTablet
      width: calc(100vw - 16px)
      .slick-track
        gap: 16px
    >.side-item
      min-width: 32%
      @include onDesktopLg
        min-width: 24%
      @include onDesktopSm
        min-width: 31%
      @include onTablet
        min-width: 50%
      @include onMobileSm
        min-width: 100%
    .side-item
      height: auto
      margin-right: 16px
      @include onTablet
        margin-right: 0
    .slick-dots
      display: flex
      bottom: 0
      @include onTablet
        justify-content: center
      li
        display: block
        width: 24px
        height: 8px
        border-radius: 4px
        background: rgba(33, 52, 106, 1)
        transition: 0.2s
        @include onMobile
          height: 4px
        button
          width: 100%
          height: 100%
          &::before
            content: ''
      .slick-active
        display: block
        width: 40px
        height: 8px
        border-radius: 4px
        background: var(--text-yellow)
        @include onMobile
          height: 4px
        button
          width: 100%
          height: 100%
          &::before
            content: ''
  .slide
    display: flex
    align-items: center
    justify-content: center
  .trading__info-slide
    display: flex
    align-items: center
    justify-content: center
    width: 100%
    padding: 0 40px
    @include onDesktopSm
      margin-right: 40px
      padding: 0 20px
    img
      width: auto
      object-fit: contain
      max-height: 36px
      max-width: 100%
      @include onDesktopSm
        max-height: 28px
        width: 100%

.slick-track
  display: flex !important
  align-items: stretch