.subscribe-block
  border-top: 1px solid #DDD  
  .subscribe-block__wrapper
    display: flex
    align-items: center
    width: 100%
    justify-content: space-between
    .subscribe-block__title
      flex: 0 20%
      display: flex
      align-items: flex-end
      h2
        color: #001140
        width: 100%
        font-size: 24px
        line-height: 1.5
      b, strong
        color: var(--text-pink)
        font-weight: 500
        white-space: nowrap
    .subscribe-block__form
      position: relative
      max-width: 1151px
      width: 100%
      .wpcf7-form.invalid
        .wpcf7-response-output
          display: none
      .wpcf7-form.sent
        .wpcf7-response-output
          position: absolute
          width: 100%
          height: auto
          display: flex
          align-items: center
          color: #001140
          overflow: hidden
          margin: 0 !important
          padding: 24px 48px !important
          font-size: 32px
          font-weight: 500
          line-height: 48px
          border-radius: 8px
          background: #F4F8FD
          border: none !important
          z-index: 3
          &::after
            content: ''
            position: absolute
            width: 130px
            height: 130px
            right: -4%
            bottom: -48%
            background-image: url("data:image/svg+xml,%3Csvg width='140' height='95' viewBox='0 0 140 95' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M138.715 14.3429C140.08 11.3233 138.744 7.75094 135.648 6.56967C122.558 1.57513 108.542 -0.621012 94.5077 0.150939C78.4734 1.03292 62.8873 5.76347 49.0677 13.9425C35.248 22.1215 23.6014 33.5084 15.1129 47.14C7.68328 59.0711 2.86423 72.4147 0.943842 86.293C0.489642 89.5755 2.9784 92.4656 6.28217 92.7221L34.1981 94.89C37.5019 95.1466 40.3576 92.6673 40.9427 89.4056C42.2836 81.9313 45.0346 74.7608 49.0677 68.284C54.1609 60.105 61.1488 53.2729 69.4406 48.3655C77.7324 43.4581 87.084 40.6197 96.7046 40.0906C104.323 39.6715 111.933 40.7102 119.13 43.1313C122.271 44.1878 125.818 42.8774 127.183 39.8578L138.715 14.3429Z' fill='%23FFD400'/%3E%3C/svg%3E%0A")
            background-repeat: no-repeat
            background-size: contain
          @include onDesktopSm
            height: 90%
            font-size: 24px
            line-height: 32px
            &::after
              bottom: -66%
          @include onTabletLg
            height: 100%
            border-radius: 8px
          @include onTablet
            font-size: 18px
            line-height: 24px
          @include onMobile
            font-size: 16px
            line-height: 20px
            padding: 12px 60px 12px 24px !important
            &::after
              width: 80px
              height: 80px
              bottom: -60%

    .subscribe-block__formContainer
      display: flex
      gap: 48px
      align-items: flex-end
      p
        display: flex
        flex-grow: 1
        span
          display: flex
          flex-grow: 1
      .subscribe-block__input
        flex-grow: 1
        padding: 19px 29px 18px 80px
        border-bottom: 1px solid #AAA
        color: #001140
        font-family: Qanelas
        font-size: 16px
        font-weight: 400
        line-height: 2.0
        background-image: url("../img/icon/message_gray.svg")
        background-repeat: no-repeat
        background-position: left 15px top 50%
        background-size: 40px
        transition: 0.3s
        margin-bottom: 6px
        &:focus
          border-color: var(--text-blue)
        @include onTabletLg
          margin-bottom: 0
    form
      display: flex
      gap: 48px
      align-items: flex-end
      p:nth-child(2)
        flex-grow: 1
        span
          display: flex
          align-self: end
          .subscribe-block__input
            flex-grow: 1
            padding: 19px 29px 18px 80px
            border-bottom: 1px solid #AAA
            color: #001140
            font-family: Qanelas
            font-size: 16px
            font-weight: 400
            line-height: 2.0
            background-image: url("../img/icon/message_gray.svg")
            background-repeat: no-repeat
            background-position: left 15px top 50%
            background-size: 40px
            transition: 0.3s
            margin-bottom: 6px
            &:focus
              border-color: var(--text-blue)
            @include onTabletLg
              margin-bottom: 0
    form
      p:nth-child(3)
        .wpcf7-spinner
          display: none
    @include onDesktopSm
      gap: 24px
      .subscribe-block__formContainer
        gap: 24px
        .subscribe-block__input
          height: 87px
          padding: 58px 0px 28px 88px
          background-position: left 24px top 38px
      form
        gap: 24px
        p:nth-child(2)
          span
            .subscribe-block__input
              height: 87px
              padding: 58px 0px 28px 88px
              background-position: left 24px top 38px

    @include onTabletLg
      flex-direction: column

      .subscribe-block__title
        h2 
          font-size: 24px
          line-height: 1.25
      .subscribe-block__form
        border-radius: 8px 
        border: 1px solid #EEF1FA
        overflow: hidden
        transform: 0.3s
        &:has(.subscribe-block__input:focus )
          border-color: var(--text-blue)

      .subscribe-block__formContainer
        align-items: center
        justify-content: space-between
        gap: 12px
        .subscribe-block__input
          width: 100%
          border-bottom: none
          background-size: 24px
          height: 56px
          padding: 16px 0px 16px 60px
          background-position: left 24px top 50%
          font-size: 14px
      form
        align-items: center
        justify-content: space-between
        gap: 12px
        p:nth-child(2)
          span
            .subscribe-block__input
              width: 100%
              border-bottom: none
              background-size: 24px
              height: 56px
              padding: 16px 0px 16px 60px
              background-position: left 24px top 50%
              font-size: 14px
