.info-section
  background: var(--bg-gradient)
  overflow: hidden
  position: relative
  color: var(--text-dark)
  h1 b,
  h2 b,
  h3 b,
  h1 strong,
  h2 strong,
  h3 strong
    color: var(--text-pink)
    font-weight: 500
  &.bg-dark, &.bg-dark-gradient
    background: var(--bg-main)
    color: var(--white)
    h1 b,
    h2 b,
    h3 b,
    h1 strong,
    h2 strong,
    h3 strong
      color: var(--text-yellow)
      font-weight: 500
    .slider .slide img
      filter: invert(99%) sepia(41%) saturate(2%) hue-rotate(338deg) brightness(107%) contrast(100%)
    .slider.icon-yellow .slide img
      filter: brightness(0) saturate(100%) invert(83%) sepia(52%) saturate(2358%) hue-rotate(359deg) brightness(100%) contrast(111%)
  &.bg-reverse-gradient
    background: linear-gradient(180deg, #FFF 0%, #DCE5FF 100%)

.info__container
  display: flex
  flex-direction: column
  &.row-reverse
    flex-direction: column-reverse
    .my-slider
      margin-top: -24px
      margin-bottom: 38px
    &.row-reverse
      flex-direction: column-reverse
      .my-slider
        margin-top: 48px
        margin-bottom: 0
  &.padding-132
    padding: 132px 0
    @include onDesktopSm
      padding: 52px 0
    @include onTablet
      padding: 64px 0
    @include onMobile
      padding: 48px 0
  @include onDesktopSm
    gap: 40px
  &.with-device
    position: relative 
    &:before
      content: ''
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      width: 856px
      height: 1055px
      background-image: url(../img/info-block-device.svg)
      background-repeat: no-repeat
.content__block
  position: relative
  align-items: center
  display: flex
  gap: 80px
  padding: 116px 0
  &.padding-y-50
    padding: 50px 0
  &.padding-0
    padding: 0
    @include onMobile
      padding-bottom: 48px
  &.padding-top-0
    padding-top: 0
  &.gap-80
    gap: 80px
    @include onMobile
      gap: 16px
  &.gap-35
    gap: 35px
    @include onMobile
      gap: 16px
  @include onDesktopSm
    gap: 40px
    padding: 40px 0
  .content__block-img
    &.with-border
      border-radius: 16px
      overflow: hidden
  .content__block-slider
    position: relative
  &.image-out 
    .content__block-text
      width: initial
      max-width: 927px
      @include onDesktopSm
        width: 60%
      @include onTablet
        width: 100%
    .content__block-img
      position: relative
      height: 500px
      @include onTablet
        height: auto
        &.is-hide-mobile
          display: none
      &::before
        content: ""
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        display: block
        width: 1032px
        height: 1032px
        border-radius: 1042px
        opacity: 0.3
        background: var(--bg-blue)
        filter: blur(250px)
      img
        position: absolute
        top: 0
        right: -100%
        width: 200%
        @include onTablet
          right: -100px
          position: relative
  &.row-reverse
    flex-direction: row-reverse
    &.image-out 
      .content__block-text
        width: initial
        max-width: 927px
      .content__block-img
        width: auto
        img
          position: relative
          left: -68%
          width: 200%
  &.text-column
    align-items: start
    justify-content: space-between
    .content__block-text
      min-width: auto
      width: 35%
      z-index: 2
      &:nth-child(2n)
        margin-top: 8%
        width: 33%
        min-width: auto
    .content__block-img
      position: absolute
      top: 0
      left: 0
      transform: translate(21%, 6%)
      z-index: 1
    .content__block-img-about
      position: absolute
      top: 0
      left: 0
      width: auto
      transform: translate(-1%, 6%)
      z-index: 1
      @include onDesktopSm
        transform: translate(-82%, 30%)
        left: 50%
        width: 450px
    @include onTablet
      flex-direction: column
      .content__block-text
        width: 100%
        &:nth-child(2n)
          margin-top: 4%
          width: 100%
      .content__block-img
        position: relative
        transform: translate(-30%, 98px)
      .content__block-img-about
        position: relative
        transform: translate(-80%, 98px)
      
  .btn
    width: fit-content
    margin-top: 16px
    @include onMobile
      width: 100%
  &.big-text-side
    gap: 80px
    @include onMobile
      gap: 16px
    .content__block-text
      width: initial
      max-width: 927px
    .content__block-img
      width: 40%
    .content__block-text
      width: 60%
    @include onTablet
      .content__block-img, .content__block-text
        width: 100%
  @include onTablet
    flex-direction: column-reverse
    gap: 24px
    padding: 64px 0
    &.row-reverse
      flex-direction: column-reverse
  @include onMobile
    padding: 48px 0
    
.content__block-text
  min-width: 50%
  display: flex
  flex-direction: column
  gap: 32px
  @include onDesktopSm
    gap: 16px
  @include onTablet
    width: 100%
  .items-paper
    li
      border-radius: 8px
      background: #FFF
      padding: 24px
  li
    position: relative
    display: flex
    gap: 8px
    p 
      flex: 1
    &:before
      content: ''
      position: relative
      top: 6px
      display: block
      width: 18px
      height: 18px
      background-image: url('../img/icon/ellipse.svg')
      background-repeat: no-repeat
      background-size: contain
      @include onDesktopSm
        top: 2px
    &:not(:last-child) 
      margin-bottom: 16px

.content__block-img
  min-width: 50%
  display: flex
  align-items: center
  justify-content: center
  gap: 32px
  @include onTablet
    width: 100%
    max-width: 420px
  img
    width: 100%
    object-fit: contain

.info-section.bg-dark
  .sub-content__block
    background: rgba(63, 84, 245, 0.30)
    .sub-content__block-label
      color: #FFD400