//global
*,
*::after,
*::before
    margin: 0
    padding: 0
    box-sizing: inherit

body
    box-sizing: border-box // Moved it here and added inherit on the * selector
    background: white
    font-family: var(--main-font-family)
    overflow-x: hidden 

::-webkit-scrollbar-thumb
  background-color: #CBD1D7
  border: 4px solid rgba(0, 0, 0, 0)
  background-clip: padding-box
  border-radius: 16px
::-webkit-scrollbar 
  width: 14px
  background-color: transparent
  border-radius: 10px
  border: 4px solid rgba(0, 0, 0, 0)
  background-clip: padding-box
::-webkit-scrollbar:hover, ::-webkit-scrollbar:active
  background-color: #F8F8F8
  border-width: 3px
::-webkit-scrollbar-thumb:hover 
  background-color: #A4AFB8
  border-width: 3px
::-webkit-scrollbar-thumb:active 
  background-color: #516578

.main
  display: flex
  flex-direction: column
  justify-content: space-between

.sections__container
  height: 100%
  display: block
  min-height: 500px

main
  flex-grow: 1

a
    text-decoration: none
    color: inherit

li
    list-style: none

input
    border: 0
    outline: none

h1
  font-size: 72px
  font-weight: 500
  line-height: 1.18
  @include onDesktopSm
    font-size: 34px
    line-height: 50px
  @include onMobileSm
    font-size: 28px
    line-height: 1.3

h2
  font-size: 56px
  font-weight: 500
  line-height: 70px
  @include onDesktopSm
    font-size: 28px
    line-height: 1.3
  @include onMobileSm
    font-size: 22px

h3
  font-size: 40px
  font-weight: 500
  line-height: 60px
  @include onDesktopSm
    font-size: 18px
    line-height: 1.5
  @include onMobileSm
    font-size: 16px
    line-height: 1.3

h4
  font-size: 32px
  font-weight: 500
  line-height: 1.4
  @include onDesktopSm
    font-size: 24px
    font-weight: 400
    line-height: 1.5
  @include onTabletLg
    font-size: 18px
    font-weight: 400
    line-height: 1.5

p
  font-size: 24px
  font-weight: 400
  line-height: 36px
  @include onDesktopSm
    font-size: 16px
    line-height: 24px

.small-body
  p
    font-size: 16px
    line-height: 2.0
    @include onMobile
      font-size: 12px
      line-height: 1.66

.container
  max-width: 1648px
  padding: 0 24px
  width: 100%
  position: relative
  margin: 0 auto
  @include onDesktopSm
    max-width: 1200px
  @include onMobile
    padding: 0
    width: calc(100% - 32px)
  &.smallest
    max-width: 1360px
    padding: 0 15px

.container-intro
  flex-grow: 1
  display: flex
  flex-direction: column
  max-width: 1774px
  width: 100%
  position: relative
  

.section__container
  padding-top: 128px
  padding-bottom: 128px
  @include onDesktopSm
    padding-top: 60px
    padding-bottom: 60px
  @include onMobile
    padding-top: 48px
    padding-bottom: 48px

.banner
  background: #001140
  position: relative
  display: block
  overflow: hidden

.section
  position: relative
  margin: 0

.bg-blue-tint
  background-color: #f1f2fe

.bg-light-blue
  background: linear-gradient(180deg, #EDF2FF 0%, var(--white) 100%)

.bg-dark-event
  background: radial-gradient(77.56% 91.38% at 50.00% 79.57%, #062067 0%, #001140 100%)
  color: var(--white)

img,
object
  object-fit: contain
  height: auto
  width: 100%
  pointer-events: none

.mobile-br
  display: none
  @include onMobile
    display: block

.bg-dark
  background: radial-gradient(77.56% 50.00% at 50.00% 50.00%, #011E70 0%, #001140 100%)
  overflow: hidden
  color: var(--white)
  position: relative

.search
  display: flex
  align-items: center
  justify-content: space-between
  background-color: rgba(255, 255, 255, 0.08)
  padding: 20px 40px
  border-radius: 40px
  gap: 16px
  border: 1px solid rgba(185, 193, 255, 0.32)
  max-width: 1063px
  width: 100%
  backdrop-filter: blur(5px)
  @include onDesktopSm
    padding: 10px 20px
  input
    flex-grow: 1
    font-size: 20px
    background-color: transparent
    color: var(--white)
    font-weight: 400
    &::placeholder
      color: rgba(255, 255, 255, 0.6)
    @include onDesktopSm
    font-size: 16px
  button
    display: block
    width: 24px
    height: 24px
    background-color: transparent
    border: none
    img
      width: 100%
      object-fit: contain

.loading
  position: relative
  width: 32px
  height: 32px
  animation: load 2s linear infinite
  margin: 0 auto

@keyframes load
  0%
    transform: rotate(0)
  100%
    transform: rotate(360deg)

.author-info
  display: flex
  gap: 40px
  align-items: center
  .author
    display: flex
    align-items: center
    gap: 16px
    .avatar
      width: 48px
      height: 48px
      border-radius: 50%
      overflow: hidden
      background-color: var(--white)
      img
        width: 100%
        height: 100%
        object-fit: contain
        object-position: center
  p
    margin-bottom: 0
    font-size: 24px
    color: var(--white)
    opacity: 0.5

