.article-body
  display: flex
  flex-direction: column
  gap: 80px
  padding: 160px 0 140px
  @include onDesktopSm
    gap: 40px
  @include onMobile
    padding: 100px 0

.article-img__main
  position: absolute
  bottom: -60px
  width: 100%
  max-width: 1632px
  margin: 0 auto
  height: 400px
  border-radius: 16px
  overflow: hidden
  padding: 0 15px
  img
    width: 100%
    height: 100%
    object-fit: cover
    border-radius: 16px
    object-position: top
  @include onDesktopSm
    height: 220px
    bottom: -35px

.article-content
  display: flex
  align-items: center
  gap: 80px
  @include onDesktopSm
    gap: 45px
  @include onMobile
    flex-direction: column
    gap: 40px
  h4
    font-size: 40px
    line-height: 1.4
    font-weight: 500
    margin-bottom: 0
    text-align: left
    width: 100%
    @include onMobile
      font-size: 28px
  p
    font-size: 24px
    font-weight: 400
    line-height: 1.5
    @include onMobile
      font-size: 20px
  .article-img
    min-width: 480px
    height: 360px
    border-radius: 16px
    overflow: hidden
    img
      width: 100%
      height: 100%
      object-fit: cover
      object-position: center
    @include onDesktopSm
      min-width: 300px
    @include onMobile
      min-width: 100%
      height: 280px
  .article-text
    gap: 24px
    display: flex
    flex-direction: column
    width: 100%
  &.reverse
    flex-direction: row-reverse
    @include onMobile
      flex-direction: column
  &.column
    flex-direction: column
    gap: 40px
    .article-img
      width: 100%
      min-width: 100%
    h4
      margin-bottom: 12px

  ul
    display: flex
    flex-direction: column
    gap: 8px
    padding: 8px 0
    li
      padding: 18px 24px
      background-color: #F4F8FD
      font-size: 24px
      line-height: 1.4
      width: fit-content
      @include onMobile
        padding: 12px 20px
        font-size: 18px

.quote-item
  display: flex
  gap: 48px
  @include onMobile
    gap: 20px
  .quote-img
    display: block
    min-width: 72px
    @include onMobile
      min-width: 40px
    img
      width: 100%
      object-fit: contain
  .quote-text
    display: flex
    flex-direction: column
    gap: 20px
    max-width: 1008px
  p
    font-weight: 500
    font-size: 32px
    line-height: 1.5
    @include onMobile
      font-size: 20px
  .quote-author
    font-size: 24px
    font-weight: 500