.footer
    padding: 64px 0 48px
    background-color: var(--bg-main)
    position: relative
    @include onDesktopSm
      padding: 40px 0
    .footer__top-wrapper
      display: flex
      gap: 24px
      z-index: 1
      @include onMobile
        display: block
    .logo-footer__wrapper
      min-width: 388px
      gap: 80px
      display: flex
      flex-direction: column
      a
        display: block
        position: relative
      .social-contacts__social
        display: flex
        align-items: center
        gap: 20px
        @include onDesktopSm
          gap: 16px
          a
            width: 24px
            height: 24px
      @include onDesktopSm
        min-width: 157px
        gap: 40px
      @include onMobile
        display: none
    .logo-footer
      display: block
      width: 116px
      height: 51px
      position: absolute
      z-index: 3
      cursor: pointer
      @include onMobile
        display: none
    .footer__policy
      color: var(--white)
      display: flex
      justify-content: space-between
      align-items: center
      margin-bottom: -4px
      
      p
        line-height: 1.5
        opacity: 0.3
        transition: 0.3s
      @include onDesktopSm
        p
          font-size: 14px
      @include onMobile
        justify-content: center
        p 
          font-weight: 600
          line-height: 1.67
        
      .policy_link
        display: flex
        gap: 24px
        a
          text-decoration-line: underline
          &:hover
            p
              opacity: 1
          &:active
            p 
              opacity: 0.6
        @include onMobile
          justify-content: center
          p:last-child
            display: none
            

      .policy_developed
        display: flex
        gap: 8px
        position: relative
        z-index: 6
        @include onMobile
          display: none
      .logo-developed
        width: 104px
        height: auto
    .footer__social-contacts
      display: flex
      flex-direction: column
      gap: 43px
      @include onMobile
        position: relative
        margin-top: 24px
        gap: 24px
        text-align: end
        padding: 0
        p 
          font-size: 12px
          font-weight: 500
          line-height: 1.66

    .social-contacts__wrapper
      display: flex
      justify-content: flex-end
      flex-grow: 1
      @include onMobile
        justify-content: space-between
      .social-contacts__social
        gap: 24px
        align-items: center
        position: relative
        z-index: 2
        display: none
        @include onMobile
          display: flex
        a
          width: 27px
          height: 27px
          @include onMobile
            width: 32px
            height: 32px
      .social-contacts__contacts
        position: relative
        z-index: 5
        display: flex
        flex-direction: column
        a
          color: var(--white)
          line-height: 2.0
          transition: 0.3s
          text-align: end
          @include onDesktopSm
            p
              font-size: 14px
          &:hover
            color: var(--text-yellow)

    .footer-navigation
      position: relative
      z-index: 4
      display: flex
      flex-direction: column
      gap: 33px
      flex-grow: 1
      @include onDesktopSm
        margin-bottom: 40px
        gap: 20px
      @include onMobile
        gap: 24px
        margin-bottom: 24px
    .footer-navigation__menu
      display: flex
      gap: 112px
      @include onDesktopSm
        gap: 15px 30px
        flex-wrap: wrap
      @include onMobile
        flex-direction: column
        gap: 8px
      .footer-navigation__list
        position: relative
        .accordionContent 
          display: flex
          flex-direction: column
          justify-content: flex-start
        h4
          color: var(--text-yellow)
          font-size: 24px
          font-weight: 400
          line-height: 1.2
          margin-bottom: 21px
          @include onDesktopSm
            margin-bottom: 10px
            font-size: 18px
        a
          color: var(--white)
          opacity: 0.5
          line-height: 2.0
          transition: 0.3s
          @include onDesktopSm
            font-size: 14px
          &:hover
            opacity: 1
          &:active
            opacity: 0.8
         
        @include onMobile
          padding: 16px 0px
          h4
            font-size: 20px
            font-weight: 500
            line-height: 1.4
            margin-bottom: 0px
          .accordionContent
            margin-top: 16px
            
              
        .accordionTitle:after
          display: none
          @include onMobile
            display: block
    .footer-navigation__action
      display: flex
      gap: 24px
      a img
        height: 24px
        width: 24px
        filter: brightness(0) invert(1)
      a:hover img 
        filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(20deg) brightness(103%) contrast(101%) invert(82%) sepia(54%) saturate(3104%) hue-rotate(1deg) brightness(107%) contrast(109%)
      @include onDesktopSm
        flex-wrap: wrap
        order: -1
      
      .footer-navigation__link
        display: flex
        gap: 10px
        align-items: center
        justify-content: center
        padding: 11px 48px 11px 24px
        border-radius: 4px
        border: 1px solid rgba(255, 255, 255, 0.20)
        transition: 0.3s
        svg rect, svg path, svg circle
            stroke:  #FFF
            transition: 0.3s
        p
          color: #FFF
          font-family: Qanelas
          font-size: 16px
          font-weight: 400
          line-height: 1.2
          position: relative
          transition: 0.3s
          display: flex
          gap: 10px
          align-items: center
          &:after
            content: ''
            display: block
            width: 16px
            height: 16px
            background-image: url('../img/icon/arrow_double_right.svg')
            background-repeat: no-repeat
            transform: translateX(0)
            transition: transform 0.3s ease
        
        &:hover
          border: 1px solid rgba(255, 212, 0, 0.40)
          svg rect, svg path, svg circle
            stroke: #FFD400
          p 
            color: #FFD400
            &:after
              transform: translateX(4px)
              filter: brightness(0) saturate(100%) invert(78%) sepia(17%) saturate(4729%) hue-rotate(3deg) brightness(110%) contrast(103%)
        &:active
          background: rgba(255, 212, 0, 0.08)
      @include onMobile
        flex-direction: column
        gap: 15px
