.btn
  display: flex
  justify-content: center
  text-align: center
  font-family: Poppins
  border-radius: 40px
  white-space: nowrap
  cursor: pointer
  transition-duration: 0.3s
  transition-property: transform, background, box-shadow, border-color, font-weight, color
  &--with-arrow
    display: flex
    align-items: center
    justify-content: center
    gap: 16px
    @include onMobile
      gap: 8px
    &::after
      content: ''
      display: block
      width: 24px
      height: 24px
      background-image: url(../img/icon/btn-arrow-right.svg)
      background-size: contain
      background-repeat: no-repeat
      transition: 0.3s
    &:hover
      &::after
        transform: translateX(4px)
  &--pink
    background: var(--text-pink)
    color: var(--white)
    border: 2px solid var(--text-pink)
    box-shadow: var(--shadow-button-pink)
    padding: 23px 47px
    font-size: 24px
    font-weight: 400
    line-height: 16px
    @include onDesktopSm
      padding: 16px 36px
      font-size: 16px
      font-weight: 500
      line-height: 20px
    &:hover
      background: linear-gradient(0deg, rgba(0, 17, 64, 0.16) 0%, rgba(0, 17, 64, 0.16) 100%), #FF007B
      box-shadow: var(--shadow-button-pink-hover)
      border-color: #d60372
    &:active
      background: linear-gradient(0deg, rgba(0, 17, 64, 0.24) 0%, rgba(0, 17, 64, 0.24) 100%), #FF007B
      box-shadow: var(--shadow-button-pink-active)
      border-color: #a30666
  &--sm
    padding: 15px 27px
    font-size: 16px
    font-weight: 500
    line-height: 20px
    &.btn--pink
      &:hover
        box-shadow: var(--shadow-button-pink-hover-sm)
      &:active
        box-shadow: var(--shadow-button-pink-active-sm)
  
  &--form
    border-radius: 64px
    box-shadow: 0px 10px 50px 0px rgba(255, 0, 123, 0.20)
  &--icon
    width: 32px
    height: 32px
    transition: 0.3s
    &:hover
      filter: brightness(130%)
      box-shadow: 0px 8px 32px 0px rgba(255, 212, 0, 0.24)
    &:active
      filter: none
    &--fb
      background-image: url(../img/icon/facebook.svg)
      background-size: contain
      background-repeat: no-repeat
    &--in
      background-image: url(../img/icon/linkedin.svg)
      background-size: contain
      background-repeat: no-repeat
    &--twi
      background-image: url(../img/icon/twitter.svg)
      background-size: contain
      background-repeat: no-repeat
  &--subscribe
    border-radius: 8px
    border: 1px solid var(--white)
    color: transparent
    background: #F4F8FD
    width: 80px
    height: 80px
    background-image: url("../img/icon/paper-plane_pink.svg")
    background-repeat: no-repeat
    background-position: center
    background-size: 40px
    &:hover
      border-width: 2px
      background: #E9F1FB
      box-shadow: 0px 8px 12px 0px rgba(0, 17, 64, 0.04), 0px 2px 4px 0px rgba(0, 17, 64, 0.04), 0px 1px 2px 0px rgba(0, 17, 64, 0.04)
      background-image: url("../img/icon/paper-plane_pink.svg")
      background-repeat: no-repeat
      background-position: center
      background-size: 40px
    &:active
      background: #E9F1FB
      box-shadow: 0px 6px 6px 0px rgba(0, 17, 64, 0.04) inset
      background-image: url("../img/icon/paper-plane_pink.svg")
      background-repeat: no-repeat
      background-position: center
      background-size: 40px
    @include onDesktopSm
      width: 87px
      height: 87px
    @include onMobile
      width: 56px
      height: 56px
      background-size: 24px
      &:hover
        background-size: 24px


.read_more_link, .read_more_link-yellow
  position: relative
  padding: 8px 0
  color: #3F54F5
  font-size: 24px
  font-weight: 400
  line-height: 16px
  justify-content: center
  text-align: center
  font-family: Poppins
  border-radius: 40px
  white-space: nowrap
  cursor: pointer
  transition-duration: 0.3s
  transition-property: color
  display: flex
  align-items: flex-end
  flex-grow: 1
  gap: 8px
  max-width: fit-content
  &::after
    content: ''
    display: block
    width: 24px
    transition: 0.2s
    height: 24px
    position: relative
    background-image: url("../img/icon/btn-arrow-right.svg")
    background-size: contain
    background-repeat: no-repeat
    filter: brightness(0) saturate(100%) invert(23%) sepia(91%) saturate(1911%) hue-rotate(225deg) brightness(104%) contrast(99%)
  &::before
    content: ''
    position: absolute
    bottom: 0
    display: block
    width: 100%
    height: 1px
    transition: 0.3s
    background: #3F54F5
  &:hover
    &::after
      transform: translateX(4px)
  @include onDesktopSm
    font-size: 16px
  &:hover
    color: #3700D3
    &::after
      filter: brightness(0) saturate(100%) invert(17%) sepia(70%) saturate(4768%) hue-rotate(249deg) brightness(78%) contrast(116%)
    &::before
      background: #3700D3
    &:active
      color: #3004AF
      font-weight: 500
      &::after
        filter: brightness(0) saturate(100%) invert(13%) sepia(42%) saturate(4237%) hue-rotate(240deg) brightness(126%) contrast(120%)
      &::before
        background: #3004AF

.read_more_link-yellow
    color: var(--text-yellow)
    font-size: 32px
    &::after
      filter: brightness(0) saturate(100%) invert(79%) sepia(50%) saturate(927%) hue-rotate(358deg) brightness(100%) contrast(104%)
    @include onDesktopSm
      font-size: 20px
    &::before
      background: var(--text-yellow)
    &:hover
      color: var(--white) !important
      &::after
        filter: brightness(0) saturate(100%) invert(100%) sepia(7%) saturate(5%) hue-rotate(163deg) brightness(100%) contrast(106%)
      &::before
        background: var(--white)
    &:active
      color: #DDB800 !important
      font-weight: 500
      &::after
        filter: brightness(0) saturate(100%) invert(85%) sepia(20%) saturate(7437%) hue-rotate(7deg) brightness(95%) contrast(100%) !important
      &::before
        background: #DDB800 !important 

.divider
  display: none
  height: 1px
  width: 100%
  background: rgba(255, 255, 255, 0.16)
  @include onMobile
    display: block

/*Accordion*/
.accordionBlock
  
  .accordionTitle
    position: relative
    cursor: pointer
    &:after
      content: ''
      display: block
      width: 24px
      height: 24px
      background-image: url('../img/icon/arrow_down.svg')
      background-repeat: no-repeat
      position: absolute
      top: 50%
      right: 0
      transform: translateY(-50%) rotate(0deg)
      transition: transform .25s ease
.active.accordionBlock .accordionTitle:after
  transform: translateY(-50%) rotate(180deg)

/* Tabs */
ul.js-tabs-nav li 
  float: none
  cursor: pointer

/* Custom select*/
.custom-select 
  position: relative
  .dropdown-toggle
    position: relative
    &:after
      content: ''
      display: block
      width: 24px
      height: 24px
      background-image: url('../img/icon/arrow_down_dark.svg')
      background-repeat: no-repeat
      position: absolute
      top: 50%
      right: 2%
      transform: translateY(-50%) rotate(0deg)
      transition: transform .25s ease
      cursor: pointer
    &.open
      &:after
        transform: translateY(-50%) rotate(180deg)
  .dropdown-menu
    display: none 
    position: absolute
    z-index: 50
    left: 0
    top: 10%
    margin:0
    width: 100%
