.header__block
  display: flex
  align-items: center
  justify-content: space-between

.logo
  display: block
  width: 116px
  height: 51px
  @include onTabletLg
    width: 85px
    height: 38px
  @include onTablet
    width: 63px
    height: 28px

.burger-button
  display: none

@include onTablet
  .logo
    width: 63px
    height: 28px
  .burger-button
    display: block
    cursor: pointer
    width: 24px
    height: 24px

  .header__nav
    display: none

.header
  position: fixed
  left: 0
  right: 0
  z-index: 1000
  padding: 38px 0 20px
  background: transparent
  transition: 0.3s
  @include onMobile
    padding: 20px 0
  &.fixed__header
    padding: 20px 0
    background: #011240
    @include onMobile
      padding: 12px 0
  &__buttons
    display: flex
    align-items: center
    gap: 16px
    &--mobile
      display: none
    @include onTabletLg
      gap: 8px
      .btn
        font-size: 12px
        padding: 10px 16px
    @include onTablet
      &--desktop
        display: none
      &--mobile
        display: flex
        align-items: center
        gap: 24px

.header__nav
  .nav-menu
    display: flex
    align-items: center
    gap: 72px
    @include onDesktopSm
      gap: 20px
    >li
      display: flex
      align-items: center
      gap: 8px
      transition: 0.3s
      position: relative
      padding-top: 16px
      padding-bottom: 16px
      overflow: hidden
      >a
        color: var(--white)
        font-size: 16px
        font-weight: 600
        text-transform: uppercase
        display: block
        height: 100%
        @include onDesktopSm
          font-size: 14px
        @include onTabletLg
          font-size: 12px
          height: auto
      &.menu-item-has-children::after
        cursor: pointer
        content: ''
        width: 16px
        height: 16px
        background-image: url(../img/icon/arrow-down-white.svg)
        transition: transform 0.3s
      &:hover
        overflow: visible
        &::after
          filter: brightness(0) saturate(100%) invert(79%) sepia(50%) saturate(927%) hue-rotate(358deg) brightness(100%) contrast(104%)
          transform: rotate(180deg)
        .sub-menu
          opacity: 1
          height: auto
          padding: 8px
          transition: opacity 0.3s ease-in-out
          @include onTablet
            padding: 0
        >a
          color: var(--text-yellow)
          @include onTablet
            color: var(--text-dark)

.sub-menu
  position: absolute
  top: calc(100% - 10px)
  left: -33px
  min-width: 400px
  border-radius: 8px
  background: var(--bg-main)
  box-shadow: 0px 20px 80px 0px rgba(157, 169, 255, 0.08)
  z-index: 1
  opacity: 0
  height: 0
  overflow: hidden
  @include onDesktopSm
    min-width: 240px
  @include onTablet
    height: auto
  li
    display: flex
  a
    display: block
    padding: 22px 26px
    color: var(--white)
    border-radius: 8px
    font-size: 20px
    font-weight: 400
    line-height: 24px
    opacity: 0.6
    transition: 0.3s
    position: relative
    &::after
      content: ""
      position: absolute
      bottom: 8px
      left: 22px
      right: calc( 100% - 22px )
      height: 1px
      background: var(--text-yellow)
      transition: 0.2s
    &:hover
      color: var(--text-yellow)
      opacity: 1
      &::after
        right: 22px
    @include onDesktopSm
      font-size: 14px
      padding: 10px 16px
      &::after
        left: 16px
        right: calc( 100% - 16px )
        &:hover
          &::after
            right: 16px
    @include onTabletLg
      font-size: 12px
      font-weight: 400
      opacity: 0.5
      padding: 8px
      border-radius: 6px
      &::after
        left: 8px
        right: calc( 100% - 8px )
        &:hover
          &::after
            right: 8px

  .nav-menu
    display: flex
    align-items: center
    gap: 72px
    @include onDesktopSm
      gap: 32px
  .nav-menu>li
    position: relative
    display: flex
    align-items: center
    gap: 8px
    padding: 10px 0
    cursor: pointer
    &>a
      color: var(--white)
      font-size: 16px
      font-weight: 600
      line-height: 32px
      text-transform: uppercase
      transition: 0.3s

.header__menu
  position: fixed
  right: -100%
  top: 0
  bottom: 0
  padding: 112px 56px 56px
  background: var(--white)
  z-index: 9999
  max-width: 337px
  width: 100%
  transition: all 1s ease
  overflow-y: auto

.header__menu-close
  position: absolute
  left: 16px
  top: 20px
  cursor: pointer
  width: 24px
  height: auto

.header_menu__active
  right: 0
  transition: all 0.5s ease
.header_menu_wrap
  overflow-y: auto
  padding: rem(60px) rem(30px)
  height: 100%
  @media screen and (max-width: 768px)
    padding: rem(20px) rem(15px)

.shadow
  z-index: -1
  position: fixed
  top: 0
  left: 0
  right: 0
  bottom: 0
  transition: 0.5s
  &--active
    z-index: 9
    background: rgba(81, 101, 120, 0.12)
    backdrop-filter: blur(5px)

.header__menu-list
  display: flex
  flex-direction: column
  justify-content: space-between
  height: 100%
  gap: 32px
  .nav-menu
    display: flex
    flex-direction: column
    width: 225px
    gap: 32px
    li
      display: flex
      align-items: start
      flex-direction: column
      cursor: pointer
      width: 100%
      position: relative
      .sub-menu
        padding-top: 9px
      >a
        position: relative
        width: 100%
        text-transform: uppercase
        color: #001140
        font-size: 16px
        font-weight: 600
        line-height: 32px
        white-space: nowrap
        // &:hover
        //   background-color: var(--white)
        //   color: var(--text-dark)
      &.menu-item-has-children>a::after
        content: ''
        width: 16px
        height: 16px
        background-image: url(../img/icon/arrow-down-white.svg)
        filter: brightness(0) saturate(100%) invert(12%) sepia(50%) saturate(1514%) hue-rotate(195deg) brightness(93%) contrast(117%)
        position: absolute
        top: 50%
        right: 0
        transform: translateY(-50%) rotate(270deg)
        transition: transform 0.25s ease
      &.active>a::after
        transform: translateY(-50%) rotate(360deg)
    .sub-menu
      position: static
      background: transparent
      display: flex
      flex-direction: column
      margin-left: -16px
      border-radius: 4px
      z-index: 1
      min-width: 100%
      box-shadow: none
      opacity: 1
      a
        display: block
        padding: 7px 16px 9px
        color: #001140
        border-radius: 8px
        font-size: 16px
        font-weight: 400
        line-height: 24px
        opacity: 0.5
        transition: 0.3s
        text-transform: none
        width: auto
        &::after
          left: 16px
          right: calc( 100% - 16px ) !important
        &:hover
          opacity: 1
          &::after
            right: 16px !important

.body-with-menu
  overflow: hidden

.header__menu-bottom
  display: flex
  flex-direction: column
  gap: 32px
  .header__menu-social-media
    display: flex
    align-items: center
    gap: 24px
  .header__menu-contacts
    display: flex
    flex-direction: column
    .header__menu-contact
      color: #001140
      font-size: 14px
      font-weight: 500
      line-height: 20px
