.paper__title
  img
    width: auto
.subscribe-block
  padding-bottom: 34px
  padding-top: 29px

.feedback-block__paper
  max-width: 876px
  margin: 0 auto
  &:before
    content: ''
    position: absolute
    width: 750px
    height: 925px
    top: -100px
    left: -50px
    background-image: url('../img/device_feedback-block-1.png')
    background-repeat: no-repeat
    opacity: 0.3
    background-size: contain
    animation: rotating 15s linear infinite
    transform-origin: center 50%
    z-index: 1
    @include onDesktopSm
      width: 675px
      height: 832.5px
      top: -40px
      left: -180px
    @include onMobile
      width: 386px
      height: 475px
      top: 50%
      left: -16px
      transform: translateY(-50%)
  .feedback-inner__container
    position: relative
    margin: 0 auto
    max-width: 1293px
    border-radius: 24px
    border: 1px solid #E8F1FC
    overflow: hidden
    color: var(--text-dark)
    z-index: 2
    &:has(.success-message.show)
      border-color: transparent
    @include onTablet
      flex-direction: column
      box-shadow: 0px 16px 40px 0px rgba(0, 0, 0, 0.10)

.paper
  flex: 0 0 50%
  padding: 72px 48px 64px
  background: #F4F8FD
  z-index: 2
  .paper__title
    margin-bottom: 16px
    text-align: center
    h4
      color: var(--text-blue)
      text-transform: uppercase
    p
      margin: 0 auto
      font-size: 20px
      line-height: 1.4
  @media screen and (max-width: 1330px)
    padding: 62px 38px 38px
  @include onTablet
    flex: 1
    padding: 28px 16px 48px
    display: flex
    flex-direction: column
    gap: 16px
  .icon-container
    display: inline-block
    width: 24px
    height: 24px
    transition: all 0.3s
    svg path, svg rect
      stroke: #929292
      transition: 0.3s
  .dropdown-menu__item:hover,
  .tabs .tabs-nav__item:hover
    cursor: pointer
    p
      color: #001140
    .icon-container
      svg path, svg rect
        stroke: var(--text-pink)

  .tabs
    margin-top: 16px
    display: flex
    flex-direction: column
    gap: 28px
    @include onDesktopSm
      margin-top: 0
      gap: 16px
    .tabs-nav
      display: flex
      gap: 8px
      justify-content: space-between
      //padding: 16px 8px
      @media screen and (max-width: 1330px)
        flex-direction: column
      @include onDesktopSm
        display: none
      p
        color: #929292
        line-height: 1.2
        transition: 0.3s
      .tabs-nav__item
        position: relative
        padding: 16px 8px
        display: flex
        gap: 4px
        align-items: center
        justify-content: center
        flex-grow: 1
        &:after
          content: ''
          position: absolute
          bottom: 0
          left: 0
          right: 0
          width: 100%
          height: 1px
          background: #3F54F5
          opacity: 0.3
      .tabs-nav__item.active
        &:after
          opacity: 1
        p
          color: #001140
        .icon-container
          svg path, svg rect
            stroke: var(--text-pink)
  .custom-select
    p
      line-height: 1.2
      @include onDesktopSm
        font-size: 16px
        line-height: 1.2

    .dropdown-toggle
      display: none
      @include onDesktopSm
        display: block
        &:after
          right: 18px
    .dropdown-toggle .dropdown-menu__item
      border-bottom: 1px solid #3F54F5
      color: #001140
      svg path, svg rect
        stroke: var(--text-pink)

    .dropdown-menu
      top: 58px
      background: #F4F8FD
      padding-bottom: 16px
      box-shadow: 0px 16px 40px 0px rgba(0, 0, 0, 0.10)
      display: none

    .dropdown-menu__item
      display: flex
      gap: 4px
      align-items: center
      padding: 16px
      border-bottom: 1px solid rgba(63, 84, 245, 0.30)
      color: #929292
      transition: 0.3s
      p
        transition: 0.3s
      &:hover
        border-bottom: 1px solid #3F54F5

.feedback-block__formContainer
  display: flex
  flex-direction: column
  gap: 24px
  @include onDesktopSm
    gap: 16px
  p
    font-size: 14px
  .feedback-block__radio
    display: flex
    justify-content: space-between
    gap: 8px
    margin-bottom: 28px

    label
      span
        color: #929292
        font-family: Qanelas
        font-size: 16px
        font-weight: 400
        line-height: 1.2
        text-transform: uppercase
        padding: 16px 8px 16px 40px
        border-bottom: 1px solid rgba(63, 84, 245, 0.30)
        transition: 0.6s ease
    input[type="radio"]:checked + span
      color: #001140
      border-bottom: 1px solid #3F54Fe
    input
      display: none

  .wpcf7-form
    display: flex
    flex-direction: column

  .wpcf7-form input[type='text'],
  .wpcf7-form input[type='email'],
  .wpcf7-form input[type='number'],
  .wpcf7-form input[type='tel'],
  .wpcf7-form textarea,
  .feedback-block__input
    color: #001140
    font-family: Qanelas
    font-size: 16px
    font-weight: 400
    line-height: 2.0
    border: 1px solid transparent
    border-radius: 6px
    background: var(--white)
    padding: 12px 16px
    width: 100%
    height: 56px
    transition: 0.3s
    &::placeholder
      color: var(--text-input-label)
    &:focus
      border-color: var(--text-blue)

    &.outlined-placeholder
      height: 72px
      padding: 32px 18px 16px 24px
      @include onMobile
        height: 56px
        padding: 20px 16px 12px
      &.calendar
        background-image: url(../img/icon/calendar.svg)
        background-repeat: no-repeat
        background-position: right 20px center
        color: rgba(146, 156, 178, 0.3)
        text-transform: uppercase
        &::-webkit-datetime-edit-year-field:not([aria-valuenow]),
        &::-webkit-datetime-edit-month-field:not([aria-valuenow]),
        &::-webkit-datetime-edit-day-field:not([aria-valuenow]) 
          color: var(--text-input-label)
        &.with-value
          color: var(--text-dark)
        &:focus,
        &:not(:placeholder-shown)
          background-image: none
        @include onMobile
          background-position: right 20px center

  input[type='number']::-webkit-outer-spin-button,
  input[type='number']::-webkit-inner-spin-button
    -webkit-appearance: none
    margin: 0
  .wpcf7-form textarea
    height: 200px
    outline: none
    resize: none
    padding: 32px 24px 16px
    @include onMobile
      padding: 30px 16px 16px
      &.two-row
        padding: 32px 16px
  .wpcf7-form-control-wrap
    display: flex
    flex-direction: column
    .wpcf7-list-item
      margin-left: 0
  .wpcf7-form-control label
    display: flex
    gap: 8px
    cursor: pointer
  input[type='checkbox']
    height: 20px
    width: 20px
  .nice-select
    color: #001140
    font-family: Qanelas
    font-size: 16px
    font-weight: 400
    line-height: 2.0
    background: var(--white)
    padding: 12px 16px
    width: 100%
    height: 56px
    .current
      color: var(--text-input-label)
      &.has-option
        color: #001140
  input[type='submit']
    margin-top: 16px
    margin-left: auto
    margin-right: auto
    background-image: url("data:image/svg+xml,%3Csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.5 12L21.2071 11.2929L21.9142 12L21.2071 12.7071L20.5 12ZM5.5 13C4.94771 13 4.5 12.5523 4.5 12C4.5 11.4477 4.94771 11 5.5 11V13ZM15.2071 5.29289L21.2071 11.2929L19.7929 12.7071L13.7929 6.70711L15.2071 5.29289ZM21.2071 12.7071L15.2071 18.7071L13.7929 17.2929L19.7929 11.2929L21.2071 12.7071ZM20.5 13H5.5V11H20.5V13Z' fill='white'/%3E%3C/svg%3E%0A")
    background-repeat: no-repeat
    background-position: right 48px center
    background-size: 24px
    padding-right: 88px
    cursor: pointer
    &:hover
      background-position: right 30px center
    @include onMobile
      margin-left: 0
      margin-right: 0
  .wpcf7-form.sent
    .wpcf7-response-output
      position: absolute
      margin: 0 !important
      padding: 100px 134px !important
      left: -65%
      top: 26%
      color: #3F54F5
      font-size: 72px
      font-weight: 500
      line-height: 84px
      border-radius: 24px
      border: 1px solid rgba(255, 255, 255, 0.10) !important
      background: rgba(255, 255, 255, 0.50)
      box-shadow: 0px 30px 80px 0px rgba(17, 22, 37, 0.08), 0px 6px 20px 0px rgba(17, 22, 37, 0.04), 0px 2px 8px 0px rgba(17, 22, 37, 0.04)
      backdrop-filter: blur(24px)
      white-space: nowrap
      &::after
        content: ''
        position: absolute
        width: 150px
        height: 150px
        right: 5%
        bottom: 5%
        background-image: url("data:image/svg+xml,%3Csvg width='144' height='136' viewBox='0 0 144 136' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.147767 127.477C-0.54556 130.717 1.51774 133.922 4.78871 134.452C21.7872 137.209 39.2048 136.279 55.8528 131.695C74.4318 126.58 91.5057 117.066 105.63 103.957C119.755 90.8485 130.515 74.531 137 56.3848C142.811 40.1246 145.037 22.8247 143.554 5.66802C143.269 2.36662 140.227 0.070277 136.944 0.520237L101.278 5.40858C97.9945 5.85854 95.7243 8.88584 95.8994 12.1949C96.4024 21.6996 95.0167 31.2307 91.8002 40.2309C87.9091 51.1186 81.4531 60.9091 72.9783 68.7743C64.5034 76.6395 54.2591 82.3481 43.1117 85.4172C33.8969 87.9542 24.2891 88.6258 14.8483 87.416C11.5614 86.9948 8.37338 89.0332 7.68005 92.2736L0.147767 127.477Z' fill='%23FFD400'/%3E%3C/svg%3E%0A")
        background-repeat: no-repeat
        background-size: contain
      @include onDesktopSm
        left: -75%
      @include onTablet
        padding: 90px 70px !important
        left: 50%
        top: 50%
        transform: translate(-50%, -50%)
        font-size: 56px
        line-height: 56px
        &::after
          width: 100px
          height: 100px
      @include onMobile
        padding: 80px 50px !important
        font-size: 32px
        line-height: 32px
        &::after
          width: 80px
          height: 80px


.feedback-block__formContainer
  .request-form
    input[type='submit']
      margin-top: 0

.feedback-block__paper.feedback-block__paper--secondary
  position: relative
  .paper__top
    display: flex
    flex-direction: column
    align-items: center
    gap: 24px
    .paper__sub-title
      max-width: 440px
      display: block
      text-align: center
      p
        font-size: 24px
  .wpcf7-form.sent
    .wpcf7-response-output
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      white-space: nowrap
      @include onTabletLg
        padding: 90px 70px !important
        font-size: 56px
        line-height: 56px
        &::after
          width: 100px
          height: 100px
      @include onMobile
        padding: 80px 50px !important
        font-size: 32px
        line-height: 32px
        &::after
          width: 80px
          height: 80px


  .wpcf7-list-item
    margin: 0
    text-align: center
    color: #929292
    font-size: 14px
    line-height: 1.43
  .paper
    padding: 48px
    @include onTablet
      padding: 30px

.add-checkbox-container
  display: flex
  flex-direction: column
  gap: 16px
  p
    text-transform: inherit
    opacity: 0.5
  span
    margin-bottom: 0
    font-size: 14px

.accept-terms
  display: flex
  gap: 12px
  input
    display: none
    opacity: 0
    position: absolute
  .checkbox-indicator
    position: relative
    cursor: pointer
    display: block
    min-width: 20px
    height: 20px
    border-radius: 2px
    border: 1px solid #B6B8BF
    background-color: transparent
    transition: 0.2s
    &::before
      content: ""
      position: absolute
      top: 40%
      left: 50%
      transform: translate(-50%, -50%) rotate(45deg)
      border-right: 2px solid var(--white)
      border-bottom: 2px solid var(--white)
      transition: 0.2s
      height: 12px
      width: 8px
  input:checked ~ .checkbox-indicator
    background-color: var(--bg-blue)
  p
    font-size: 14px
    text-transform: initial
    text-align: left
    padding: 0
    margin: 0

.success-message--overall
  display: none
.success-message--overall.show
  position: absolute
  border-radius: 24px
  backdrop-filter: brightness(20%) saturate(100%) invert(100%) sepia(90%) saturate(1923%) hue-rotate(180deg) brightness(114%) contrast(100%)
  padding: 32px 24px
  width: 100%
  height: 100%
  display: flex
  align-items: center
  justify-content: center
  z-index: 3
  &.success-message__contact
    background: #f4f8fd
  .success-message__container
    display: flex
    flex-direction: column
    align-items: center
    max-width: 1030px
    text-align: center
    h2
      color: #FF007B
      font-size: 56px
      font-weight: 500
      line-height: 1.25
      margin-bottom: 32px
    h4
      color: #001140
      font-size: 32px
      font-weight: 500
      line-height: 1.5
      max-width: 935px
      margin-bottom: 64px
    p
      color: #001140
      font-size: 24px
      font-weight: 400
      line-height: 1.5
      margin-bottom: 64px
      b
        color: #001140
        font-size: 16px
        font-weight: 400
        line-height: 1.25
    @include onTablet
      h2
        font-size: 42px
        margin-bottom: 16px
      h4
        font-size: 28px
        font-weight: 500
        margin-bottom: 24px
      p
        font-size: 24px
        margin-bottom: 24px
        b
          font-size: 20px