.expectations-block
  background: #001140
  .expectations-block-container
    display: flex
    flex-direction: column
    align-items: center
    gap: 74px
    @include onDesktopSm
      gap: 45px
    @include onMobile
      gap: 24px
    h2
      text-align: center
      color: var(--white)
    .tabs
      display: flex
      flex-direction: column
      gap: 40px
      width: 100%
      @include onDesktopSm
        gap: 22px
      @include onMobile
        gap: 16px
      .tab__triggers
        .tabs__nav
          display: flex
          align-items: center
          justify-content: space-between
          gap: 16px
          overflow-x: auto
          overflow-y: hidden
          scrollbar-gutter: stable
          &::-webkit-scrollbar-thumb
            border-radius: 16px
            background-color: #A8CAD2
          &::-webkit-scrollbar
            width: 4px
          &::-webkit-scrollbar-track
            border-radius: 30px
            background-color: lightgrey
            border: 15px solid #001140
          @include onTablet
            --webkit-overflow-scrolling: touch
            &::-webkit-scrollbar
              width: 0
              height: 0
            
          @media screen and (min-width: 769px)
            &::-webkit-scrollbar-thumb
              border: 12px solid transparent
              background-clip: padding-box
              border-radius: 30px
            &::-webkit-scrollbar
              height: 33px
              border-radius: 20px
              border: 4px solid transparent
              background-clip: padding-box
          @include onTablet
            overflow-x: scroll
            overflow-y: hidden
            white-space: nowrap
            margin-right: -16px
          li
            display: flex
            justify-content: center
            position: relative
            padding: 24px 15px 36px
            cursor: pointer
            flex-grow: 1
            @include onDesktopSm
              padding: 16px 0
            @include onMobile
              padding: 0 16px 8px
            &:last-child
              @include onTablet
                margin-right: 24px
              @include onMobile
                margin-right: 16px
            &::before
              content: ""
              display: block
              position: absolute
              bottom: -25%
              width: 80%
              height: 50%
              background: #FFD400
              border-radius: 50%
              opacity: 0.24
              filter: blur(20px)
              opacity: 0
              transition: 1s
            &::after
              content: ''
              display: block
              position: absolute
              bottom: 0
              width: 100%
              height: 4px
              background: rgba(255, 255, 255, 0.10)
              transition: 0.3s
            &:hover
              p
                color: var(--text-yellow)
                opacity: 0.78
            &.active
              &::before
                opacity: 0.24
              @include onMobile
                background: transparent
              &::after 
                background: var(--text-yellow)
                opacity: 1
              p
                opacity: 1
                color: var(--text-yellow)
            p
              display: inline-block
              width: 100%
              color: var(--white)
              opacity: 0.4
              text-transform: uppercase
              text-align: center
              transition: 0.3s
              @include onTabletLg
                font-size: 14px
                white-space: nowrap
                line-height: 1.5
              @include onTablet
                font-size: 18px
      .tabs__content
        .tab__content-box
          display: flex
          justify-content: space-between
          align-items: flex-start
          padding: 96px
          border-radius: 16px
          border: 1px solid rgba(255, 255, 255, 0.20)
          background: radial-gradient(62.73% 40.44% at 90.97% 91.84%, rgba(1, 30, 112, 0.37) 0%, rgba(0, 15, 56, 0.45) 100%)
          @include onDesktopSm
            padding: 45px
          @include onTablet
            position: relative
            padding: 24px 24px 32px
            // height: 572px
            z-index: 10
            overflow: hidden
          .tab__content-desc
            display: flex
            flex-direction: column
            gap: 72px
            max-width: 608px
            width: 100%
            @include onDesktopSm
              gap: 45px
              max-width: 480px
            @include onTablet
              gap: 182px
            @include onMobile
              width: 100%
              height: 100%
              justify-content: space-between
            .tab__content-text
              display: flex
              flex-direction: column
              gap: 40px

              @include onDesktopSm
                gap: 16px
                a
                  font-size: 18px
              @include onTablet
                gap: 24px
              h4
                color: var(--white)
                @include onMobile
                  max-width: 290px
                  line-height: 30px
                  
            .btn
              display: flex
              align-items: center
              gap: 16px
              max-width: fit-content
              @include onTablet
                max-width: 100%
          .tab__content-image
            margin-top: -55px
            @include onTabletLg
              margin-top: 0
            @include onTablet
              position: absolute
              max-width: 411px
              z-index: -1
              opacity: 0.1
              bottom: 0
              right: 0
            @include onMobile
              bottom: -10%
              right: -25%